let hosts = "http://proxy-mainnet.doschain.org/insight";
// let hosts2 = "http://mainnet.doschain.org/insight";
if (window.location.host !== "games.doschain.org") {
  hosts = "http://35.220.198.141:8087";
  // hosts = "http://10.0.0.15:8083";
  // hosts2 = "https://testnet.doschain.org/insight";
}

async function requestWrapper(request) {
  return new Promise(async (resolve, reject) => {
    try {
      var res = await request;
      resolve(JSON.parse(res));
    } catch (e) {
      reject(e);
    }
  });
}

/** 用户信息 **/
export const getUser = (address) => {
  return window.demos.request({
    method: "GET",
    url: `${hosts}/v1/api/user/${address}`,
  });
};

/** 绑定 **/
export const bindCode = (address, code) => {
  return window.demos.request({
    method: "POST",
    url: `${hosts}/v1/api/user/${address}/bind`,
    data: { code },
  });
};

/** 激活 **/
export const activeUser = (address, rawtx) => {
  return window.demos.request({
    method: "POST",
    url: `${hosts}/v1/api/user/${address}/active`,
    data: { rawtx },
  });
};

/** 示例 **/
export const requestExample = (price, num, code) => {
  return requestWrapper(window.demos.allin.submit(price, num - 0, code, ""));
};

/** 矩阵列表 **/
export const getMatrixList = ({ addr, type = 0 }) => {
  return window.demos.request({
    method: "GET",
    url: `${hosts}/v1/api/matrix/${addr}/${type}`,
  });
};

/** 购买矩阵 **/
export const doBuyMatrix = ({ id, addr, type }) => {
  return window.demos.request({
    method: "POST",
    url: `${hosts}/v1/api/matrix/${addr}/${type}/${id}/pay`,
  });
};
