import React, { useEffect, useState } from "react";
import "./style.scss";
import { connect, MaskLoading, Card, CheckHashModal, Modal, LanguageSwitch } from "../../components";
// import {HashRouter as Router, Link} from "react-router-dom";
import { toast, parseQuery, showError/*, addZero*/ } from "../../utils";
import { getUser, bindCode, activeUser as _active } from "../../service"
import QRCode from 'qrcode';
let root = document.getElementById( 'root' );
let timer = null;

function Home({ address, t, history}) {
  const invite = parseQuery.getQuery('invite') || '';
  const [QRC, setQRC] = useState('');
  const [top, setTop] = useState(null);
  const [state, setState] = useState(null);
  const [showShare, setShowShare] = useState(false);
  const [showRecommend, setShowRecommend] = useState(false);
  const [showCheckHash, setShowCheckHash] = useState(false);
  const [isLoading, setIsLoading] = useState(false); //开通loading
  const [recommend, setRecommend] = useState(invite); //推荐地址
  const [hash, setHash] = useState(''); //激活hash

  function handleOpenShare(){
    setShowShare(true);
  }

  function handleCloseShare(){
    setShowShare(false);
  }

  function preventTouchStart() {
    let banner = document.getElementById('home-banner');
    let width = document.body.clientWidth;
    let height = banner.clientHeight - 705 * (width / 750) ;
    setTop(height  + 'px');
  }

  useEffect(() => {
    setTimeout(preventTouchStart);
    // root.addEventListener( 'touchstart', preventTouchStart , false );
    if (!address) return;
    initState();
    // return () => {
    //   root.removeEventListener('touchstart', preventTouchStart);
    // };
  }, [address]);

  function copyAddress () {
    try {
      const input = document.getElementById('address-container');
      input.select();
      document.execCommand('Copy');
      toast(t('copy_success'));
      handleCloseShare();
    } catch (e) {
      toast(t('copy_failed'));
    }
  }

  function handleChangeRecommend(ev){
    setRecommend(ev.target.value);
  }

  async function initQRCode(code) {
    let location = window.location;
    try {
      const QRC = await QRCode.toDataURL(`${location.origin}${location.pathname}/?orientation=portrait&invite=${code}`, { width: 350, margin: 0 });
      setQRC(QRC);
    } catch (err) {
      console.error(err);
    }
  }

  function initState() {
    setIsLoading(true);
    getUser(address).then(async (data) => {
      console.log(data);
      setIsLoading(false);
      if (data.code === 200) {
        setState(data.data);
        let user = data.data.user;
        let state = data.data.state;
        if (user.hash && user.state === 0) {
          setHash(user.hash);
          validateTrade(user.hash);
        }
        if(user.state === 1){
          window.sessionStorage[`white_crow_${address}`] = JSON.stringify({
            price: state.price,
            address: state.address,
            qoute: state.qoute,
          });
        }
        await initQRCode(user.code);
      } else if(data.code === 1001)  {
        setShowRecommend(true);
      } else {
        showError(data.code);
      }
    }, (err) => {
      console.warn(err);
      setIsLoading(false);
    });
  }

  function scan () {
    window.demos.scanQRCode().then(function (data){
      let addr = data;
      if(data.includes('invite=')) {
        addr = data.match(/invite=(\w+)/)[1]
      }
      setRecommend(addr);
    });
  }

  function bind() {
    if(!address) return;
    let code = recommend.trim();

    if (!code) {
      toast('请填写推荐ID');
      return;
    }

    setIsLoading(true);
    bindCode(address, code).then(function (data) {
      console.log(data);
      setIsLoading(false);
      if(data.code === 200) {
        toast('绑定成功');
        setShowRecommend(false);
        initState();
      } else {
        showError(data.code, t);
        // toast('绑定失败');
      }
    }, function (_) {
      setIsLoading(false);
    })
  }

  // 确认交易数
  function validateTrade (hash) {
    if (!hash) return;
    try {
      async function check () {
        console.log('demos.dosdata.expand.transaction');
        const { confirmations } = await window.demos.dosdata.expand.transaction(hash);
        console.log('confirmations: ' + confirmations);
        if (confirmations && confirmations > 1) {
          setHash('');
          initState();
        } else {
          clearTimeout(timer);
          timer = setTimeout(check, 2000);
        }
      }
      clearTimeout(timer);
      timer = setTimeout(check, 2000);
    } catch (errMsg) {
      console.error(errMsg);
    }
  }

  function checkHash () {
    setShowCheckHash(true);
  }

  async function activeUser(){
    let rawtx = await window.demos.contractRaw(
        "send",
        state.state.qoute,
        [
          {type:"address", value: state.state.address},
          {type: "uint256", value: state.state.price * 2},
        ]
    ).catch(function (err) {
      toast(err.toString());
    });

    if (!rawtx) {
      toast('激活失败');
      return;
    }

    setIsLoading(true);
    await _active(address, rawtx).then((data) => {
      console.log(data);
      setIsLoading(false);
      if (data.code === 200){
        toast('激活成功');
        setHash(data.data);
        validateTrade(data.data);
      } else {
        showError(data.code);
      }
    },()=>{
      setIsLoading(false);
      toast('激活失败');
    });

  }

  function goMatrix() {
    history.push('/matrix');
  }

  return (
      <div className="p-home">
        <LanguageSwitch />
        <div className='home-wrapper'>
          {top !== null && <div className='home-banner-polyfill' style={{top: top}} />}
          <div className="home-banner" id='home-banner'>
            <p>{t('white_crow')}</p>
          </div>
          {state && (
              <Card>
                <ul className='home-info clearFloat'>
                  <li>
                    <strong>{state.state.num}</strong>
                    <p>{t('home_info1')}</p>
                  </li>
                  <li>
                    <strong>+{state.state.num24h}</strong>
                    <p>{t('home_info2')}</p>
                  </li>
                  <li>
                    <strong>{state.state.value}</strong>
                    <p>{t('home_info3')}</p>
                  </li>
                  <li>
                    <strong>{state.state.value24h}</strong>
                    <p>{t('home_info4')}</p>
                  </li>
                </ul>
                <div className='home-info2'>
                  <div>{state.user.value || '0.00'} DOS</div>
                  <div>
                    <div>ID {state.user.id}</div>
                    <div>{state.user.num}</div>
                  </div>
                  <div><strong>X3</strong><span>{state.user.value3x || '0.00'} DOS</span></div>
                  <div><strong>X4</strong><span>{state.user.value4x || '0.00'} DOS</span></div>
                </div>
              </Card>
          )}

          {state && (
              <div className='home-bottom'>
                <button onClick={handleOpenShare}>{t('share')}</button>
                <div />
                {state.user.state === 0
                    ? <button onClick={hash ? checkHash : activeUser}>{t('active')}</button>
                    : state.user.state === 1
                        ? <button onClick={goMatrix}>{t('buy')}</button>
                        : <button disabled>{t('user_disabled')}</button>
                }
              </div>
          )}

        </div>

        {state && (
            <Modal visible={showShare} onCancel={handleCloseShare} className='home-qrCode-modal'>
              <p className='p1'>{t('affiliate_link')}</p>
              <div className='home-qrCode'>
                {QRC && <img src={QRC} alt="" width="350" height="350" />}
              </div>
              <p className='p2'>{state.user.code}</p>
              <button onClick={copyAddress}>{t('copy_address')}</button>
              <input
                  type="text"
                  value={state.user.code}
                  readOnly
                  id="address-container"
                  style={{ border: 0, padding: 0, position: 'fixed', left: '-100%' }}
                  onChange={() => {}}
              />
            </Modal>
        )}

        <Modal visible={showRecommend} showClose={false} className='home-recommend-modal'>
          <p className='p1'>{t('recommended_id')}</p>
          <div className='home-input'>
            <input
                type="text"
                placeholder={t('recommended_hint')}
                value={recommend}
                onChange={handleChangeRecommend}
            />
            <div onClick={scan}/>
          </div>
          <button onClick={bind}>{t('submit')}</button>
        </Modal>

        <CheckHashModal
            hash={hash}
            visible={showCheckHash}
            onCancel={() => {setShowCheckHash(false)}}
        />

        {isLoading && <MaskLoading />}
      </div>
  );
}

export default connect()(Home);
